import { getFlows, getFlow, auditFlow } from '@/api/sku';
import SkuTable from './module/Table';
export default {
  name: "ApplyList",
  components: {
    SkuTable: SkuTable
  },
  data: function data() {
    return {
      total: 100,
      confirmModal: {
        show: false,
        loading: false
      },
      selectedFlow: null,
      action: null,
      actionSelectedSKU: [],
      selectedFlowTable: [],
      tableLoading: false,
      columns: [{
        title: '发起人',
        key: 'creator_name',
        align: 'center'
      }, {
        title: '处理状态',
        align: 'center',
        key: 'result',
        render: function render(h, params) {
          console.log(params);
          return h('div', [h('Tag', {
            props: {
              color: params.row.result ? 'green' : 'orange'
            }
          }, params.row.result ? '已处理' : '未处理')]);
        }
      }, {
        title: '创建时间',
        key: 'create_time',
        align: 'center'
      }, {
        title: '操作',
        slot: 'action',
        align: 'center'
      }],
      data: [{
        "creator_name": "万伟斌",
        "result": false,
        "flow_id": 6,
        "create_time": "2020-11-04 18:10:03"
      }]
    };
  },
  computed: {
    disableSubmit: function disableSubmit() {
      return this.selectedFlowTable.length === 0;
    },
    title: function title() {
      return this.selectedFlowTable.length === 0 ? "产品审批流程列表" : "审批列表SKU";
    }
  },
  mounted: function mounted() {
    this.getData();
  },
  methods: {
    onSelect: function onSelect(value) {
      console.log(value);
      this.actionSelectedSKU = value;
    },
    getData: function getData() {
      var _this = this;

      console.log("getData");
      getFlows().then(function (res) {
        console.log(res);
        _this.data = res.results;
        _this.total = res.count;
      });
    },
    back: function back() {
      this.selectedFlowTable = [];
    },
    audit: function audit(row) {
      var _this2 = this;

      this.selectedFlow = row;
      getFlow(row.flow_id).then(function (res) {
        console.log(res);
        _this2.selectedFlowTable = res.data.skus;
      });
      console.log(row);
    },
    submitFlowBtn: function submitFlowBtn(value) {
      console.log(value);
      this.action = value === 'pass';
      this.confirmModal.show = true;
    },
    submitFlow: function submitFlow() {
      var _this3 = this;

      var data = {
        is_accept: this.action,
        sku: this.actionSelectedSKU
      };
      auditFlow(this.selectedFlow.flow_id, data).then(function (res) {
        console.log(res);

        _this3.$message.success("审批完成");

        _this3.getData();

        _this3.back();
      });
    }
  }
};